import React from "react";
import { Formik } from "formik";
import axios from 'axios';
import OrderForm from "./edit-order.form";
import * as Yup from 'yup';
// import { DisplayFormikState } from './order.helper';

import Swal from 'sweetalert2';

const orderValidateSchema = Yup.object().shape({
	customername: Yup.string().required("Bạn chưa chọn khách hàng!"),
	order_items: Yup.array()
		.of(Yup.object().shape({
			product: Yup.string().required("Chưa chọn sản phẩm"),
			quantity: Yup.number()
				.min(1, '0 không hợp lệ')
				.required("Chưa nhập số lượng"),
			price: Yup.number()
		})
		),
	description: Yup.string()
		.min(2, 'Quá ngắn!')
})


export default class OrderHandle extends React.Component {

	constructor(props) {
		super(props);
		// this.onSubmit = this.onSubmit.bind(this);
		this.state = {
			customername: '',
			customerdata: [],
			productdata: [],
			product: '',
			quantity: '',
			price: '',
			// date: new Date().toLocaleDateString(),
			customers: [],
			products: [],
			description: ''
		}
	}

	componentDidMount() {
		if (this.props.match && this.props.match.params.id) {
			const postID = this.props.match.params.id;
			axios.get(`http://localhost:5000/orders/${postID}`)
				.then(response => { 
					this.setState({ customername: response.data.customername, });
				 }).catch(function (error) {
					console.log(error);
				})
		}

		// axios.get('http://localhost:5000/orders/' + this.props.match.params.id)
		// 	.then(response => {
		// 		this.setState({
		// 			customername: response.data.customername,

		// 		})
		// 	})
		// 	.catch(function (error) {
		// 		console.log(error);
		// 	})

		axios.get('http://localhost:5000/customers/')
			.then(response => {
				if (response.data.length > 0) {
					this.setState({
						customers: response.data.map(customer => customer.customername)
					})
				}
			});

		axios.get('http://localhost:5000/products/')
			.then(response => {
				if (response.data.length > 0) {
					this.setState({
						products: response.data.map(product => product.product)
					})
				}
			})
	}



	// SUBMIT
	handleSubmit = (values, { props = this.props, setSubmitting }) => {
		setTimeout(() => {
			this.addOrder(
				values.customername,
				values.order_items,
				values.description,
				values.paid_status,
				values.date,
			);
			setSubmitting(false);
		}, 800);
	};


	async addOrder(customername, order_items, description, paid_status, date) {
		const order = {
			customername,
			order_items,
			description,
			paid_status,
			date,
		};
		console.log("Orders submitted: ", order)
		const url = `'http://localhost:5000/orders/update/' + this.props.match.params.id, order`;

		// const response = await axios.post(url, order);

		var success = true

		if (!success) {
			console.log('thua roi')
		}


		await axios.post(url, order)
			.then(res => Swal.fire({
				position: 'center',
				icon: 'success',
				title: 'Cập nhật đơn hàng thành công!',
				showConfirmButton: false,
				timer: 1000,
				onClose: window.location = '/',
				// title: res.data			
			}))
	}

	// onSubmit(e) {
	// 	e.preventDefault();
	// 	const order = {
	// 		customername: this.state.customername,
	// 		order_items: [{
	// 			product: this.state.product,
	// 			quantity: this.state.quantity,
	// 			price: this.state.price,				
	// 		}],
	// 		description: this.state.description
	// 	}
	// 	console.log("Order update: ", order);

	// 	axios.post('http://localhost:5000/orders/update/' + this.props.match.params.id, order)
	// 		.then(res => Swal.fire({
	// 			position: 'center',
	// 			icon: 'success',
	// 			title: 'Cập nhật đơn hàng thành công!',
	// 			showConfirmButton: false,
	// 			timer: 1000,
	// 			onClose: window.location = '/'
	// 			// title: res.data			
	// 		}))
	// 	// window.location = '/';
	// }



	// END SUBMIT

	render() {
		let { customers, products, customerdata, productdata } = this.state
		console.log(customerdata)
		return (
			<Formik
				enableReinitialize
				validateOnChange
				initialValues={{
					customerdata,
					productdata,
					customers,
					products,
					customername: '',
					description: '',
					paid_status: false,
					order_items:
						[{
							product: '',
							quantity: '',
							price: '',
						}],
					date: new Date().toDateString("dd-mm-yy"),
					isSubmitting: false
				}}

				validationSchema={orderValidateSchema}

				onSubmit={this.handleSubmit}
			>
				{props => (
					<>
						{/* <DisplayFormikState	{...props} /> */}

						<OrderForm {...props} />

					</>
				)}
			</Formik>
		);
	}
}
